
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import { validateNotNull } from 'util/rules'

export default {
data () {
return {
switchIndex: 0,
stock: {},
id: '',
ifPop: false,
successImg: require('../../assets/images/success_img.png'),
inited: false 
}
},
methods: {
toHome () {
this.$router.push({
path: '/stockList'
})
},
toDetail () {
this.$router.push({
path: '/stockView',
query: {
stockId: this.stock.id
}
})
},
switchTabItem (index) {
this.switchIndex = index
},
saveStock () {
if (validateNotNull(this, this.stock.manageArea, '经营面积') && validateNotNull(this, this.stock.monthRent, '店月租金') && validateNotNull(this, this.stock.monthSalary, '月人工工资')) {
if (this.stock.manageArea && (Number(this.stock.manageArea) < 0 || Number(this.stock.manageArea) > 200)) {
this.$vux.toast.text('经营面积需为0-200m²')
return
}
if (this.stock.monthRent && (Number(this.stock.monthRent) < 0 || Number(this.stock.monthRent) > 50000)) {
this.$vux.toast.text('店月租金需为0-50000元')
return
}
if (this.stock.monthSalary && (Number(this.stock.monthSalary) < 0 || Number(this.stock.monthSalary) > 50000)) {
this.$vux.toast.text('月人均工资需为0-50000元')
return
}
let ifValid = this.stock.stockProductClientDetailList.some(item => {
return (item.shopCount && (item.shopCount < 0 || !/^\d+$/.test(item.shopCount))) || (item.storehouseCount && (item.storehouseCount < 0 || !/^\d+$/.test(item.storehouseCount)))
}) // 库存填报数据，如果有填，需大于0
if (ifValid) {
this.$vux.toast.text('库存填报数据需要为>=0的整数')
return
}
if (this.stock.otherMoney && !/^\d+$/.test(this.stock.otherMoney)) {
this.$vux.toast.text('其他产品总金额需要为>=0的整数')
return
}
if (validateNotNull(this, this.stock.inventoryUser, '盘点人') && validateNotNull(this, this.stock.superviseUser, '监察人') && validateNotNull(this, this.stock.clientUser, '店主')) {
this.stock.stockProductClientDetailList.map(item => { // 库存填报数据，如果没填，默认为0
if (item.shopCount === null || item.shopCount === '') {
item.shopCount = 0
}
if (item.storehouseCount === null || item.storehouseCount === '') {
item.storehouseCount = 0
}
return item
})
this.stock.otherMoney = this.stock.otherMoney === null || this.stock.otherMoney === '' ? 0 : this.stock.otherMoney
this.$tupHttp(this, 'front/stockProduct/submit', 'POST', this.stock).then(res => {
this.ifPop = true
})
}
}
},
saveDraft (isPreview) {
if (this.inited) {
return
}












this._commitData(isPreview)
},
_commitData (isPreview) {
this.inited = true
this.$tupHttp(this, 'front/stockProduct/saveDraft', 'POST', this.stock).then(res => {
if (isPreview) {
this.inited = false
this.$router.push({
path: '/stockPreview'
})
} else {
this.inited = false
this.$vux.toast.text('保存草稿成功')
}
}, () => {
this.inited = false
})
},
_getDetail (id) {
this.$tupHttp(this, 'front/stockProduct/getDetail/' + id).then(res => {
this.stock = res.data.model
})
},
_getStockProductClientDraft () {
this.$tupHttp(this, 'front/stockProduct/getStockProductClientDraft', 'POST').then(res => {
this.stock = res.data.model
this.stock.manageArea = this.stock.manageArea ? this.stock.manageArea.toString() : this.stock.manageArea
this.stock.monthRent = this.stock.monthRent ? this.stock.monthRent.toString() : this.stock.monthRent
this.stock.monthSalary = this.stock.monthSalary ? this.stock.monthSalary.toString() : this.stock.monthSalary
})
}
},
created () {
let id = this.$route.query.stockId
if (id) { // 有id，是获取已提交的详情，对已提交进行修改
this.id = id
this._getDetail(this.id)
} else { // 无id，是对草稿进行修改
this._getStockProductClientDraft()
}
},
components: {
Tab,
TabItem,
XHeader,
XInput,
Cell,
Popup
}
}



